* {
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
  background: #000000a1;
  padding: 1.875em 1.25em 0;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.wrapper > div {
  display: flex;
  align-items: center;
  background: #fff;
  height: 2em;
  padding: 0.1em;
  border-radius: 4px;
}
.wrapper > div > input {
  height: 2em;
  background: transparent;
  border: none;
  outline: none;
  flex: 1;
}
.wrapper > div > img {
  width: 20px;
  padding-right: 3px;
  cursor: pointer;
}

.title {
  color: #fff;
  text-decoration-line: underline;
}
.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-gap: 1rem;
  background: rgb(167, 167, 167);
  padding: 1rem;
  margin-top: 25px;
}
.movie {
  display: flex;
  flex-direction: column;
  background: #eee;
  padding: 0.5em;
  border-radius: 4px;
  position: relative;
  height: 100%;
}

.movie > div:nth-of-type(2) {
  width: 100%;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  height: 100%;
}
.movie > div > img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  cursor: pointer;
}
.movie_year {
  font-size: 1em;
  font-weight: 600;
  position: absolute;
  margin: 2px 0 0 2px;
  top: 15px;
  left: 15px;
  z-index: 12;
  letter-spacing: 3px;
  background: #eee;
  padding: 2px;
  border-radius: 5px;
}
.movie_desc {
  background: rgba(0, 0, 0, 0.92);
  bottom: 8px;
  color: #eee;
  padding: 7px 0 7px 0;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.movie_desc > span {
  padding-left: 7px;
}
.movie_desc > h3 {
  padding-left: 7px;
}
@media (max-width: 31.25rem) {
  .wrapper {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .container {
    padding: 0;
  }
}
@media (max-width: 18.75rem) {
  .App {
    padding: 0;
    width: 100%;
  }
}
